<template>
    <div class="right">
        <el-carousel height="150px" indicator-position="none">
            <el-carousel-item
                v-for="item in $store.state.blog.swiperList"
                :key="item"
            >
                <span>{{ item.tips }}</span>
                <img :src="item.img_url" class="img" />
                <!-- <h3>{{ item + "---" + swipeContainer }}</h3> -->
            </el-carousel-item>
        </el-carousel>
        <div class="tags" @click="handleTags">
            <el-tag
                v-for="(item, index) in $store.state.blog.allTypeLists"
                :key="index"
                :data-type="item.name"
                type="success"
                size="small"
                >{{ item.name }}</el-tag
            >
        </div>
    </div>
</template>

<script>
export default {
    props: {
        swipeContainer: String
    },
    data() {
        return {
            loading: true
        };
    },
    mounted() {
        this.$store.dispatch("getBlogTypes");
    },
    methods: {
        handleTags(e) {
            let type = e.target.dataset.type;

            let typeLIst = localStorage.getItem("BLOG_INDEX_NAV_TYPE_LIST");
            if (typeLIst) {
                let data = JSON.parse(typeLIst);
                let item = data.filter(v => v.name === type)[0];

                if (item) return this.$router.push("/home?key=" + item.name);

                console.log(item);
                this.$message({
                    message: "没想好要做什么",
                    type: "error"
                });
            } else {
                // data = (await this.$store.dispatch("getLeftNavList")) || [];
                console.log(1);
            }
        }
    }
};
</script>

<style lang="scss" scoped>
.right {
    @media screen and (max-width: 1020px) {
        display: none;
    }
    .img {
        height: 100%;
        width: auto;
    }
    .el-carousel__item h3 {
        color: #475669;
        font-size: 18px;
        opacity: 0.75;
        line-height: 150px;
        margin: 0;
    }

    .el-carousel__item:nth-child(2n) {
        background-color: #99a9bf;
    }

    .el-carousel__item:nth-child(2n + 1) {
        background-color: #d3dce6;
    }

    width: 300px;
    height: 500px;
    background-color: #fff;
    box-sizing: border-box;
    position: relative;
    padding: 1rem;
    .tags {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        align-content: space-around;
        padding: 1rem 0;
        span {
            margin-bottom: 5px;
            cursor: pointer;
        }
    }
}
</style>