//element-ui 组件
import {
    Select,
    Option, Checkbox,
    Message, Loading, MessageBox,
    Dialog, Tag, Input, Button,
    Carousel, CarouselItem
} from 'element-ui'

import * as components from "@/components";


class RegisterComponents {
    constructor() {

    }

    install(Vue, options) {

        // 注册所有组件
        for (const key in components) {
            Vue.component(key, components[key])
        }

        Vue.prototype.$loading = Loading.service;
        Vue.prototype.$message = Message;
        Vue.prototype.$messageBox = MessageBox;

        Vue.use(Checkbox)
        Vue.use(Dialog)
        Vue.use(Tag)
        Vue.use(Input)
        Vue.use(Button)
        Vue.use(Select)
        Vue.use(Option)
        // 轮播图
        Vue.use(Carousel)
        Vue.use(CarouselItem)
    }
}


export default new RegisterComponents()