<template>
    <div class="home-index">
        <div class="left-container">
            <div class="left-outer">
                <HomeIndexLeftNav
                    :leftNavList="leftNavList"
                    :handleItem="handleItem"
                />
            </div>
        </div>
        <div class="main-right">
            <HomeIndexMiddle :middleBlogList="$store.state.blog.indexBlogs" />
            <HomeIndexRight :swipeContainer="swipeContainer" />
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            leftNavList: [],
            swipeContainer: ""
        };
    },
    inheritAttrs: false,
    created() {
        this.getLeftNavList();
    },
    watch: {
        $route: {
            handler: function(to, from) {
                //根据key 获取类型
                if (!to) {
                    let key = from.query.key;
                } else {
                    let key = to.query.key;
                    if (!key) return;

                    // 获取第一次分类的数据
                    this.$store.dispatch("getIndexPageBlog", { name: key });

                    this.leftNavList.forEach(v =>
                        v.title === key
                            ? (v.isActive = true)
                            : (v.isActive = false)
                    );
                }
            },
            immediate: true
        }
    },
    methods: {
        handleItem(e) {
            let key = e.target.dataset.key;
            this.leftNavList.forEach(v => {
                if (v.name === key) {
                    if (v.isActive === true) return;
                    v.isActive = true;
                    // 点击后获取分类数据(不需要)
                    // this.$store.dispatch("getIndexPageBlog", { name: v.name });
                    this.swipeContainer = v.title;
                    this.$router.push("/home?key=" + v.title);
                } else {
                    v.isActive = false;
                }
            });
            // 点击后保存在本地
            localStorage.setItem(
                "BLOG_INDEX_NAV_TYPE_LIST",
                JSON.stringify(this.leftNavList)
            );
            this.$store.commit("SETYPENAVLIST", this.leftNavList);
        },
        async getLeftNavList() {
            let data = (await this.$store.dispatch("getLeftNavList")) || [];

            let lastSaveItem = data.filter(v => v.isActive === true);

            if (lastSaveItem.length) {
                this.leftNavList = data;
                !this.$route.query.key &&
                    this.$store.dispatch("getIndexPageBlog", {
                        name: lastSaveItem[0].name
                    });
            } else {
                throw new Error("leftNav数据错误");
                // this.leftNavList = data.map((v, i) => {
                //     if (i === 0) {
                //         // this.$router.push("/home?key=" + v.name);
                //         this.$store.dispatch("getIndexPageBlog", {
                //             name: v.name
                //         });
                //         // 获取第一次分类的数据
                //         return { ...v, isActive: true, title: v.name };
                //     } else {
                //         return { ...v, isActive: false, title: v.name };
                //     }
                // });
            }
        }
    }
};
</script>

<style lang="scss" scoped>
.home-index {
    display: flex;
    max-width: 1180px;
    width: 100%;
    margin: 0 auto;
    flex-direction: row;
    position: relative;
    box-sizing: border-box;
    padding: 12px 0;
    .left-container {
        width: 100%;
        max-width: 96px;
        margin-right: 1rem;
        height: 100%;
        .left-outer {
            width: 100%;
            max-width: 96px;
            position: fixed;
        }
        @media screen and (max-width: 550px) {
            display: none;
        }
    }
    .main-right {
        display: flex;
        width: 100%;
        height: 100%;
        flex-direction: row;
        .rigth-fixd {
            width: 100%;
            height: 100%;
            max-width: 500px;
        }
    }
}
</style>