import { mapState } from "vuex";
import config from '@/config'



class GlobalMixIn {
    constructor() {

    }

    install(Vue, options) {

        Vue.mixin({
            data() {
                return {
                    baseIMGURL: config.baseIMGURL,
                    MyPageLeftNavList: [
                        {
                            title: "个人中心",
                            name: "个人中心",
                            component: "MyPageInfo",
                            isActive: false
                        },
                        {
                            title: "我的收藏",
                            name: "我的收藏",
                            component: "MyPageLiked",
                            isActive: false
                        },
                        {
                            name: "我的博客",
                            title: "我的博客",
                            component: "MyPageBlog",
                            isActive: true
                        },
                        {
                            name: "我的关注",
                            title: "我的关注",
                            component: "MyPageFocusPeople",
                            isActive: false
                        }
                    ]
                }
            },
            computed: {
                ...mapState({
                    headerNav: state => state.header.headerNavRouter,
                    searchValue: state => state.header.searchValue,
                    isLogin: state => state.user.isLogin,
                    user: state => state.user.user,
                })
            }
        })



    }
}


export default new GlobalMixIn()